.checkbox-container{display:block;position:relative;padding-left:28px;margin-top:12px;margin-bottom:12px;cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.checkmark{position:absolute;top:0;left:0;height:20px;width:20px;background-color:#eee;transition:all .3s ease}.checkmark:hover{background-color:#ccc}.checkmark:after{content:"";position:absolute;display:none;left:7px;top:3px;width:7px;height:12px;border:solid #fff;border-width:0 3px 3px 0;transform:rotate(45deg)}.checkmark_active{background-color:#e96e50}.checkmark_active:after{display:block}.checkmark_active:hover{background-color:#c33a18}.checkmark:focus{outline:5px auto #e96e50;outline-offset:-2px}