form{width:300px;display:flex;flex-direction:column}form .form-group{display:inline-block;margin-bottom:0;vertical-align:middle}form .form-group label{margin-bottom:10px;color:grey}form .form-group input{height:45px;width:215px;padding:12px 24px;border-radius:45px 0 0 45px;font-size:18px;box-shadow:none;border:1px solid #c1593f}form .form-group input:focus{outline:2px auto #c1593f}form .form-group input::-webkit-input-placeholder{font-size:14px}form .form-group input::-moz-placeholder{font-size:14px}form .form-group input:-ms-input-placeholder{font-size:14px}form .form-group input::-ms-input-placeholder{font-size:14px}form .form-group input::placeholder{font-size:14px}form .form-group input[type=submit]{cursor:pointer;width:85px;font-size:16px;padding:0;height:45px;color:#fff;border-radius:0 45px 45px 0;margin-left:-4px;background-color:#e96e50}